import { Button, Col, Form, Input, Row } from 'antd';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/auth/actions';

export default function HouseKeepingPage() {
	const dispatch = useDispatch();
	const history = useHistory();

	const emailRef = useRef(null);
	const passwordRef = useRef(null);
	const openingRef = useRef(null);
	const [form] = Form.useForm();

	const { loading = false, isGrantedAccess = false } = useSelector((state) => state?.auth);
	const { loading: loadingTrx = false } = useSelector((state) => state?.transaction);

	const onFinish = (values) => {
		if (isGrantedAccess) {
			dispatch({
				type: 'transaction/HOUSE_KEEPING',
				payload: {
					dateFrom: values?.dateFrom,
					dateTo: values?.dateTo
				},
				history
			});

			return;
		}

		dispatch({
			type: actions.AUTH_USER_CLEAR_DATA,
			payload: {
				email: values?.email,
				password: window.btoa(values?.password)
			}
		});
	};

	const onFinishFailed = (e) => console.log('Error! ', e);

	const onKeyDown = (e) => {
		if (isGrantedAccess) {
			if (e.key === 'Escape') {
				openingRef.current.blur();
				openingRef.current.value = '';
			}

			openingRef && openingRef.current && openingRef.current.focus();
		} else {
			const isActiveEmail = document.activeElement === (emailRef.current !== null && emailRef.current.input) || false;
			const isActivePassword = document.activeElement === (passwordRef.current !== null && passwordRef.current.input) || false;

			if (e.key === 'Enter' && isActiveEmail) {
				emailRef.current.blur();
				passwordRef.current.focus();
				return;
			}

			if (e.key === 'Enter' && isActivePassword) {
				emailRef.current.blur();
				passwordRef.current.focus();

				return;
			}

			if (e.key === 'Escape') {
				history.push('/pos/options');
				form.resetFields();
			}

			if (e.key === 'ArrowUp') {
				emailRef.current.focus();
			}

			if (e.key === 'ArrowDown') {
				passwordRef.current.focus();

				if (isActivePassword) {
					passwordRef.current.blur();
					emailRef.current.blur();
				}
			}
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);

		dispatch({
			type: 'auth/SET_STATE',
			payload: {
				isGrantedAccess: false
			}
		});

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, []);

	return (
		<div className="w-full h-screen">
			<div className="w-full h-full flex flex-col items-center justify-center">
				<div className="max-w-xl">
					<div>
						<h1 className="text-2xl font-medium">Clean All Data</h1>
						<p className="text-base leading-snug mt-3">
							Berhati-hatilah dalam menggunakan fitur ini. Semua data anda akan terhapus. Hubungi provider terlebih dahulu untuk
							memastikan bahwa anda ingin menggunakan fitur ini.
						</p>
					</div>

					{isGrantedAccess ? (
						<div className="w-full h-auto mt-12">
							<Form
								form={form}
								name="opening"
								layout="vertical"
								initialValues={{
									remember: true
								}}
								onFinish={onFinish}
								onFinishFailed={onFinishFailed}
								autoComplete="off"
								style={{ maxWidth: 'none', width: '100%' }}>
								<Form.Item label="Mulai Dari" name="dateFrom">
									<Input type="date" className="w-full h-16 rounded-md" />
								</Form.Item>
								<Form.Item label="Sampai Tanggal" name="dateTo">
									<Input type="date" className="w-full h-16 rounded-md" />
								</Form.Item>
								<div className="w-full flex items-center justify-start gap-2 mt-3">
									<Form.Item>
										<Button disabled={loadingTrx} loading={loadingTrx} htmlType="submit" className="btn-page">
											Hapus Data
										</Button>
									</Form.Item>
									<Form.Item>
										<Button
											disabled={loadingTrx}
											htmlType="button"
											className="btn-page bg-transparent text-gray-700"
											onClick={() => history.push('/pos/options')}>
											Batalkan
										</Button>
									</Form.Item>
								</div>
							</Form>
						</div>
					) : (
						<div className="w-3/4 my-7">
							<Form
								form={form}
								name="login"
								layout="vertical"
								initialValues={{
									remember: true
								}}
								onFinish={onFinish}
								onFinishFailed={onFinishFailed}
								autoComplete="off"
								style={{ maxWidth: 'none', width: '100%' }}>
								<Row wrap gutter={[8, 0]}>
									<Col span={24}>
										<Form.Item
											label="Email"
											name="email"
											rules={[
												{
													required: true,
													message: 'Wajib Diisi!'
												}
											]}>
											<Input
												ref={emailRef}
												type="email"
												autoFocus
												autoComplete="false"
												placeholder="usertest@domain.com"
												className="w-full h-12 rounded-md"
											/>
										</Form.Item>
									</Col>
									<Col span={24}>
										<Form.Item
											label="Password"
											name="password"
											rules={[
												{
													required: true,
													message: 'Wajib Diisi!'
												}
											]}>
											<Input.Password
												ref={passwordRef}
												type="password"
												autoComplete="false"
												placeholder="********"
												className="w-full h-12 rounded-md"
											/>
										</Form.Item>
									</Col>
									<Col span={18}>
										<div className="w-full flex items-center justify-start gap-2 mt-3">
											<Form.Item>
												<Button
													disabled={loading}
													loading={loading}
													type="primary"
													htmlType="submit"
													className="w-full bg-primary h-12 rounded-md hover:bg-blue-900">
													Validasi Pengguna
												</Button>
											</Form.Item>
											<Form.Item>
												<Button
													disabled={loading}
													type="default"
													htmlType="button"
													className="w-full h-12 flex items-center justify-center gap-2 rounded-md"
													onClick={() => history.push('/pos/options')}>
													Kembali <kbd className="kbd">esc</kbd>
												</Button>
											</Form.Item>
										</div>
									</Col>
								</Row>
							</Form>
						</div>
					)}
				</div>
			</div>
		</div>
	);
}
